<template>
  <div class="myCourse">
    <transition name="component-fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    name: '',
  }
</script>

<style lang="less">
  .myCourse{
    position: relative;
    .pcShow{
      display: block;
    }
    .mobileShow{
      display: none;
    }
  }
  @media screen and(max-width: 499px){
    .myCourse{
      position: relative;
      .pcShow{
        display: none;
      }
      .mobileShow{
        display: block;
      }
    }
  }
</style>



